.contacts {
  position: relative;
}

.contacts__info {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;

  pointer-events: none;

  &::before {
    @include pseudo;
    bottom: -192px;
    left: 50%;

    width: 62px;
    height: 53px;

    background-image: url("../img/map-pin.png");
    background-repeat: no-repeat;
    background-size: contain;

    transform: translateX(-50%);
  }
}

.contacts__info--close {
  &::before {
    display: none;
  }
}

.contacts__map {
  position: relative;

  display: block;
  height: 476px;
  padding-top: 76px;

  overflow: hidden;
}

.contacts__map-api {
  height: 100%;
}

.contacts__map-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  min-height: 100%;
}

@media (min-width: $tablet-width) {
  .contacts {
    padding-top: 197px;
  }

  .contacts__info {
    &::before {
      bottom: -252px;

      width: 124px;
      height: 106px;
    }
  }

  .contacts__map {
    height: 400px;
    padding-top: 0;
    overflow: hidden;
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    padding-top: 0;
  }

  .contacts__info {
    top: 95px;
    left: 50%;

    transform: translateX(-50%);

    &::before {
      bottom: 39px;
      left: 928px;

      transform: translateX(0);
    }
  }

  .contacts__map {
    height: 400px;
  }
}
